import React from 'react';
import { useNavigate } from 'react-router-dom';

import { AppContext } from '../App';

import CartItem from '../components/CartItem';



function Cart() {
    const navigate = useNavigate();

    const { cartItems, correctedPrice } = React.useContext(AppContext);
    const sumCartItems = cartItems.map((item) => item.price).reduce((prev, curr) => prev + curr, 0);

    return (
        <div className="cart">
            <div className="cart__container">
                <h1 className="cart__title"></h1>
                {cartItems.length > 0 ? (
                    <div className="cart__products">
                        <div className="cart__products-header">
                            <div className="cart__products-image"></div>
                            <div className="cart__products-title"> </div>
                            <div className="cart__products-memory"></div>
                            
                            <div className="cart__products-price"></div>
                        </div>
                        {cartItems.map((item) => (
                            <CartItem {...item} key={item.id} />
                        ))}
                        <div className="cart__info">
                            <div className="cart__info-number">
                                - : <span>{cartItems.length}</span>
                            </div>
                            <div className="cart__info-sum">
                                : <span>{correctedPrice(sumCartItems)} .</span>
                            </div>
                        </div>
                    </div>
                ) : (
                    <div className="cart__offer">
                        <h2>  </h2>
                        <p>     -</p>
                        <button className="button" onClick={() => navigate('/')}>
                              
                        </button>
                    </div>
                )}
            </div>
        </div>
    );
}

export default Cart;
